import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&â€™*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./jc-status-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class JcStatusAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;

 
jcStatusForm: FormGroup;
jcstatusId = 0;  
ajaxData : JcStatusData[];
jcStatus= new JcStatusData();
status;

msg = [];
bootstrapFromValues = [];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.jcstatusId).subscribe(params=>{
			if(params.jcstatusId != undefined){
				this.jcstatusId=params.jcstatusId; 
			}
		}); 
	console.log("jcstatusId=="+this.jcstatusId);
	if(this.jcstatusId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
	}
	}
	
    ngOnInit() {
		this.jcStatusForm = new FormGroup({			
			'jcstatusName' : new FormControl('', [ Validators.required]),
			'jcstatusDesc' : new FormControl('', [ Validators.required]),
			'jcstatusDuehrs' : new FormControl('', [ Validators.required]),
			'jcstatusTrigger1Hrs' : new FormControl(''),
			'jcstatusTrigger2Hrs' : new FormControl(''),
			'jcstatusTrigger3Hrs' : new FormControl(''),
			'jcstatusTrigger4Hrs' : new FormControl(''),
			'jcstatusTrigger5Hrs' : new FormControl(''),
			'jcstatusEntryId' : new FormControl(''),
			'jcstatusEntryDate' : new FormControl(''),
			'jcstatusModifiedId' : new FormControl(''),
			'jcstatusModifiedDate' : new FormControl(''),
		});
	}
	  
	get jcstatusName(){ return this.jcStatusForm.get('jcstatusName')};
	get jcstatusDesc(){ return this.jcStatusForm.get('jcstatusDesc')};
	get jcstatusDuehrs(){ return this.jcStatusForm.get('jcstatusDuehrs')};
	get jcstatusTrigger1Hrs(){ return this.jcStatusForm.get('jcstatusTrigger1Hrs')};
	get jcstatusTrigger2Hrs(){ return this.jcStatusForm.get('jcstatusTrigger2Hrs')};
	get jcstatusTrigger3Hrs(){ return this.jcStatusForm.get('jcstatusTrigger3Hrs')};
	get jcstatusTrigger4Hrs(){ return this.jcStatusForm.get('jcstatusTrigger4Hrs')};
	get jcstatusTrigger5Hrs(){ return this.jcStatusForm.get('jcstatusTrigger5Hrs')};
	get jcstatusEntryId(){ return this.jcStatusForm.get('jcsatusEntryId')};
	get jcstatusEntryDate(){ return this.jcStatusForm.get('jcsatusEntryDate')};
	get jcstatusModifiedId(){ return this.jcStatusForm.get('jcstatusModifiedId')};
	get jcstatusModifiedDate(){ return this.jcStatusForm.get('jcstatusModifiedDate')};
	 
	 populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<JcStatusData>(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/jcstatus-data ',
							 			JSON.stringify({ 'jcstatusId' :this.jcstatusId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.jcStatus = data['populateData'][0];
																		console.log("jcstatusModifiedId=="+this.jcStatus.jcstatusModifiedId);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }

	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['portal/customer-group-list']);      //path should be changed
		 }

 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();

			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.jcStatus[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.jcStatusForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.jcstatusId == 0){ 
				this.jcStatus['requestType'] = 'add';
			}else{
				this.jcStatus['requestType'] = 'update';
				this.jcStatus['jcstatusId'] = this.jcstatusId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/jcstatus',
									JSON.stringify(this.jcStatus),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'] == undefined){
																this.msg[0]='Error';
																this.msg[1]=data['errormsg'];
															}else{
																if(data['successmsg'].includes('Successfully')){
																	this.msg[0]='success';
																	this.msg[1]=data['successmsg'];
																}
															}
				  											this.toast.showtoast(this.msg);
															if(this.msg[0]!='Error'){
																this.jcStatusForm.reset();
																this.toggleAnimation();
															}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.jcStatus);
	  }
 
  onDelete(){
		 this.jcStatus['requestType'] = 'delete';
		this.jcStatus['jcstatusId'] = this.jcstatusId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/jcstatus',
									JSON.stringify(this.jcStatus),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.jcStatusForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}

export class  JcStatusData {
    jcstatusId:string;
	jcstatusName:string;
	jcstatusDesc:string;
	jcstatusColour:string;
	jcstatusRank:string;
	jcstatusDuehrs:string;
	jcstatusTrigger1Hrs:string;
	jcstatusTrigger2Hrs:string;
	jcstatusTrigger3Hrs:string;
	jcstatusTrigger4Hrs:string;
	jcstatusTrigger5Hrs:string;
	jcstatusEntryId: string;
	jcstatusEntryDate: string;
	jcstatusModifiedId: string;
	jcstatusModifiedDate: string;
  
}


